import React, { useEffect, useContext, useState } from 'react'
import { FaPlayCircle } from 'react-icons/fa';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Image from 'components/Image';
import Button from 'components/Button';
import BackgroundImage from 'components/BackgroundImage';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'

import AnswerContext from '../../AnswerContext'

import resbubble from './resbubble.svg';
import res1 from './res1.svg';
import res2 from './res2.svg';
import res3 from './res3.svg';
import arrow from './arrow.png';
import Layout from '../Layout'
import request from '../../utils/request'

const datas = [
  {
    title: '設\n定',
    way:['定位', '目標', '策略'],
    img: res1,
    subtitle: '前',
    buttontext: '設定'
  },
  {
    title: '互\n動',
    way:['內容', '介面', '形式'],
    img: res2,
    subtitle: '中',
    buttontext: '互動'
  },
  {
    title: '驗\n證',
    way:['認識', '認知', '認同'],
    img: res3,
    subtitle: '後',
    buttontext: '驗證'
  },
]

const Result = () => {
  const { answers, clearAnswer } = useContext(AnswerContext)
  const [answerClone, cloneAnswer] = useState([])
  useEffect(() => {
    if (answers && answers.length) {
      cloneAnswer(answers)
      request('https://us-central1-re-drive-75970.cloudfunctions.net/app/health-survey', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(answers)
      }).then(clearAnswer)
    }
  })
  const points = answerClone.reduce((res, a, i) => {
    const s = Math.floor(i / 3)
    res[s] = (res[s] || 0) + +a
    return res
  }, [])
  console.log(answerClone, points)

  return (
    <Layout height="100vh">
      <Flex height="100vh">
        <Box
          width={1 / 3}
          color="white"
          px="3.5em"
          bg="blue"
        >
          <FullpageVerticalCenter>
            <Box borderBottom="2px solid" fontSize="3em" pb="3rem">
              <Text>完成健檢後，</Text>
              <Text>深度了解，</Text>
              <Text>如何強化溝通！</Text>
            </Box>
            <Box pt="3rem" letterSpacing="0.15rem" fontSize="1.25em" lineHeight="1.65">
              依據各個階段的分數，觀察自己溝通前、中、後你最容易疏忽的問題、或是你想進一步精進的部分，點擊前往「溝通套圈圈-衛生所資訊設計指南」，了解如何強化衛生所的溝通！
            </Box>
          </FullpageVerticalCenter>
        </Box>
        <Box width={2 / 3} px={['4em', null, null, null, null, '5.625em']} bg="white">
          <FullpageVerticalCenter>
            <Flex flexDirection="column">
              {datas.map(({ title, way, img, subtitle, buttontext }, index) => (
                <Flex width={1} key={index} alignItems="center" py="1.375em" justifyContent="space-between">
                  <Flex alignItems="center">
                    <Box textAlign="center">
                      <Box
                        py="3.75rem"
                        px="0.5rem"
                        bg="blue"
                        color="white"
                        whiteSpace="pre-wrap"
                        borderRadius="1em"
                        fontSize="1.875em"
                        fontWeight="bold"
                        lineHeight="1.25"
                        position="relative"
                      >
                        {title}
                        {index !== datas.length - 1 && (
                          <Box
                            top="100%"
                            left="50%"
                            transform="translateX(-50%)"
                            position="absolute"
                          >
                            <Box mx="auto" py="1.375rem" width="2px" bg="black" />
                            <Box
                              color="black"
                              lineHeight="0"
                              transform="translateY(-90%)"
                            >
                              <Image src={arrow} />
                            </Box>
                          </Box>
                        )}
                      </Box>
                    </Box>
                    <Box textAlign="center" mx="1em">
                      {way.map((w, i) => (
                        <Box key={i}>
                          <Box fontSize="1.5em" position="relative" fontWeight="bold" color="blue">
                            {w}
                            <Box
                              position="absolute"
                              right="0"
                              top="50%"
                              fontSize="1rem"
                              transform="translate(110%, -50%)"
                            >⋯⋯</Box>
                          </Box>
                          {i !== way.length - 1 && <Box>|</Box>}
                        </Box>
                      ))}
                    </Box>
                  </Flex>
                  <Flex alignItems="center" mx="1em">
                    <Box width="7em" transform="translateX(1.5em)" zIndex={2}>
                      <BackgroundImage src={img} ratio={143 / 110} />
                    </Box>
                    <Box width="5.25em">
                      <BackgroundImage
                        src={resbubble}
                        position="relative"
                        ratio={186.59 / 83.33}
                      >
                        <Box
                          position="absolute"
                          top="50%"
                          left="50%"
                          transform="translate(-25%, -50%)"
                          color="blue"
                          fontSize="2em"
                          fontWeight="bold"
                          lineHeight="1.25"
                        >
                          溝通{subtitle}
                        </Box>
                        {index !== datas.length - 1 && (
                          <Box
                            top="100%"
                            left="50%"
                            position="absolute"
                          >
                            <Box mx="auto" py="1.55rem" width="2px" bg="black" />
                            <Box
                              color="black"
                              lineHeight="0"
                              transform="translateY(-90%)"
                            >
                              <Image src={arrow} />
                            </Box>
                          </Box>
                        )}
                      </BackgroundImage>
                    </Box>
                  </Flex>
                  <Box textAlign="center">
                    <Box fontSize="1.25em">你在溝通{subtitle}的答案
                      <Box.inline
                        mx="0.25rem"
                        width="32%"
                        fontWeight="bold"
                        bg="darkGray"
                        color="blue"
                        fontSize="1.875rem"
                        borderRadius="0.5rem"
                        verticalAlign="none"
                      >
                        {points[index]}
                      </Box.inline>
                      分
                    </Box>
                    <Box mt="1em">
                      <Button
                        rightIcon={FaPlayCircle}
                        px={['1.25em', null, null, null, null, '1.875em']}
                        borderRadius="1.5em"
                      >
                        點擊前往了解溝通{subtitle}如何{buttontext}
                      </Button>
                    </Box>
                  </Box>
                </Flex>
              ))}
            </Flex>
          </FullpageVerticalCenter>
        </Box>
      </Flex>
    </Layout>
  )
}

export default Result
